<template>
  <div class="page">
    <Header>
      <template #left>
        <button @click="callback" class="header__button header__button--back">返回</button>
      </template>
      <template #right>
        <a class="header__button link" href="javascript:void (0);">查看规则</a>
      </template>
    </Header>
    <div class="container">
      <h3>
        <a @click="$router.push('/page3')" href="javascript:void (0);">下一页</a>
      </h3>
      <p>此页未缓存</p>
      <p>
        头部右侧按钮需要自定义，未使用公共头部，引入Header组件<br/>
        返回按钮方法需要自定义，因为引入Header组件，进入该页面时，
        Header组件不会触发导航守卫（beforeRouteEnter），
        formPath就始终为null，就不能在输入地址进入页面时正常的返回!
      </p>
      <pre>
        meta: {
          title: '第二页',
          index: 3,
          keepAlive: false
        },
        components: {
          default: () => import('../views/page2.vue')
        }
    </pre>
    </div>
  </div>
</template>

<script>
import Header from '../components/Header'

export default {
  name: 'referralPartner',
  components: {
    Header
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.formPath = from.path
    })
  },
  data() {
    return {
      formPath: null
    }
  },
  created() {
  },
  methods: {
    callback() {
      if (this.formPath && this.formPath !== '/') {
        this.$router.back()
      } else {
        this.$router.replace('/')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .container {
    h3 {
      margin: rem(20) 0;
      color: $--color-primary;
      text-align: center;
    }

    p {
      padding: 0 rem(20);
      text-align: left;
    }

    pre {
      text-align: left;
    }
  }

  .link {
    color: $--color-primary;
  }
</style>
